/* Side Navigation */
.side-nav {
  height: 100%;
  width: 220px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #228B22; /* Forest Green */
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
}

.mascot-container {
  background-color: #fff;
  margin-bottom: 2rem;
  border-radius: 50%;
  padding: 20px;
  text-align: center;
}

.btn-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  padding-bottom: 30px;
}

.btn-container button {
  width: 90%;
  padding: 8px 16px;
  font-size: 16px;
  color: white;
  background: #2E8B57; /* Sea Green */
  border: none;
  cursor: pointer;
  text-align: left;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border-radius: 12px;
  margin-bottom: 8px;
  transition: background-color 0.3s, transform 0.3s;
}

.btn-container .logout-btn {
  margin-top: auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-container button:hover {
  background-color: #006400; /* Dark Green */
  transform: scale(1.05);
}

.roadmap-page {
  margin-left: 240px;
  padding: 20px;
  background-color: #F0FFF0; /* Honeydew background */
  font-family: 'Comic Sans MS', cursive, sans-serif;
  position: relative;
}

/* Roadmap Title */
.roadmap-title {
  font-size: 2.5em;
  color: #FF7F50; /* Coral */
  margin-bottom: 20px;
  text-shadow: 1px 1px 2px #000;
  animation: bounce 2s infinite;
}

/* Roadmap Container */
.roadmap-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px; /* Adjusted gap for better spacing */
  position: relative;
}

/* Roadmap Item */
.roadmap-item {
  background: linear-gradient(135deg, #76c7c0 0%, #34a0a4 100%); /* Gradient background */
  border: 2px solid #FF7F50; /* Coral */
  border-radius: 10px;
  padding: 16px;
  width: 260px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.roadmap-item.even {
  align-self: flex-start;
  margin-left: 0;
}

.roadmap-item.odd {
  align-self: flex-end;
  margin-right: 0;
}

.roadmap-item::after {
  content: '';
  position: absolute;
  width: 2px;
  height: 60px; /* Adjust height based on the gap */
  background: #FF7F50; /* Coral */
  top: 100%;
}

.roadmap-item.even::after {
  left: 50%;
  transform: translateX(-50%) rotate(135deg);
}

.roadmap-item.odd::after {
  right: 50%;
  transform: translateX(50%) rotate(-45deg);
}

.roadmap-item:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

.roadmap-item-title {
  font-size: 1.5em;
  color: #FF7F50; /* Coral */
  margin-bottom: 8px;
  text-shadow: 1px 1px 2px #000;
}

.roadmap-item-description {
  font-size: 1em;
  color: #000;
}

/* Locked Message */
.locked-message {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #ff4500; /* Orange Red */
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 0.9em;
}

/* Loading and Error Messages */
.loading,
.error {
  font-size: 1.5em;
  color: #ff4500; /* Orange Red */
  margin-top: 20px;
}

/* Unlock Animation */
.unlock-animation {
  font-size: 1.5em;
  color: #2ecc71; /* Green */
  background: #fff;
  padding: 10px 20px;
  border-radius: 10px;
  text-align: center;
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  animation: unlockAnim 3s ease-in-out;
}

@keyframes unlockAnim {
  0%, 100% {
    opacity: 0;
    transform: translateY(-20px) translateX(-50%);
  }
  50% {
    opacity: 1;
    transform: translateY(0) translateX(-50%);
  }
}

/* Bounce Animation */
@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}
